<template>
  <div class="article-md" v-if="isShow">
    <div class="v-modal"></div>
    <div class="article-model text-center ">
      <div class="title f44">金币转掘金宝说明</div>
      <div class="cten">
        <div class="cten-text f24">当天所赚取的金币会在第二天凌晨0-6点自动转为掘金宝，并累计到您的平台账户中。</div>
        <div class="redHighlight f30">掘金宝=昨日金币*昨日汇率/10000</div>
        <div class="cten-text f24">汇率与平台运营收益有关，因平台每天的运营收益不同，所以汇率也会受影响上下浮动。</div>
        <div class="clicks" @click="outVanish">知道了</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      isShow: true,
      first_tips: ''
    }
  },
  mounted () {
    this.first_tips = this.$utils.cache('first_tips')
  },
  methods: {
    outVanish () {
      this.isShow = false
    }
  }
}
</script>
<style lang="scss" scoped>
  .f44{
    font-size: 44px;
  }
  .article-md{
    position: relative;
    z-index: 100000;
  }
  .article-model{
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    width: 600px;
    height: 785px;
    background: url("../../../assets/img/read_background.png") no-repeat #fff;
    background-size: 100%;
    box-shadow: 0 2px 8px 0 rgba(74,74,74,0.50);
    border-radius: 16px;
    z-index: 100;
    .title{
      height: 165px;
      line-height: 165px;
      color: #fff;
    }
    .cten{
      margin: 0 50px;
      .cten-text{
        line-height: 50px;
        color: #232323;
      }
      .redHighlight{
        color: #E71010;
        margin: 72px 0 76px 0;
      }
    }
    .clicks{
      background: #FFAC00;
      width: 400px;
      height: 80px;
      line-height: 80px;
      border-radius: 80px;
      margin: auto;
      color: #fff;
      margin-top: 52px;
    }
  }
</style>



