<template>
<div class="scrollBox">
  <HeaderNav v-if="$utils.showNavBar()" style="z-index: 1!important" @headerShare="headerShare"></HeaderNav>
  <div id="profit" class="scrollTouch" :class="{hasHeader: $utils.showNavBar()}" >
    <div class="card">
      <div class="title">
        <div class="moneyIcon"></div><span>我的收入</span>
      </div>
      <div class="juejinMoney">
        <label class="num">{{topData.vcoin_total ? (topData.vcoin_total * 1).toFixed(2) : '0'}}</label>
        <label class="tips">当前掘金宝（个）</label>
      </div>
      <div class="moneyHistory">
        <label>今日金币 <b>{{topData.coin_today}}</b></label>
        <label>昨日金币 <b>{{topData.coin_yesterday}}</b></label>
      </div>
      <div class="bottomText posC nowrap">
        当前汇率：10000金币=1掘金宝  每晚12点自动兑换
      </div>
    </div>
    <div class="seeRich" >
      <div class="img" @click="openWin()"><img width="375" :src="income.image"></div>
    </div>
    <div class="juejinIncomeBreakdown">
      <div class="title">
        收入明细
      </div>
      <div class="tabHeader"  >
        <div v-for="(item,index) in listTab" :key="index" :class=" index===indexId ? 'active':'' " @click="toggleTab(index)">{{item}}</div>
      </div>
      <div class="list top-1px" v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="200" >
        <div class="item border-1px" v-for="(item,index) in list" :key="index">
          <div class="titleTime">
            <div>{{item.taskname}}</div>
            <div>{{$utils.timestampToStr(item.create_time * 1000, 'Y-M-D h:m:s')}}</div>
          </div>
          <div class="rightCoin">
            <div :class="'vcoin'===item.tasktype?'red':''" v-html="item.remark"></div>
          </div>
        </div>
      </div>
      <div class="no-more-data" v-if="isLoading">加载中...</div>
      <!-- <div class="no-more-data border-1px" v-if="list.length > 0 && isComplete">没有更多数据了！</div> -->
      <div class="footer">
        <span v-show="isComplete">
          最多展示近3天的收入明细
        </span>
      </div>
    </div>
  </div>
  <div class="New-footer">
    <div class="New-lt" @click="toUrl('RichList')">
      <div class="leftIcon">
        <div class="iconSize">
          <img src="../../../assets/img/my/plates.png" alt="富豪榜图片">
        </div>
      </div>
      <span>收入富豪榜</span>
    </div>
    <div class="New-rt" @click="headerShare">
      <div>
        <img src="../../../assets/img/my/coin.png" alt="">
      </div>
      晒收入分享得<i v-if="topData.exposure_to_income">{{topData.exposure_to_income.coin}}{{topData.exposure_to_income.reward_type_text}}</i>
    </div>
  </div>
  <toggleCount v-if="count"></toggleCount>
  <IndexShare @shareSuccess='shareSuccess' @getArc='getArc' :appShare='appShare' v-if="$route.path === '/my/profit'" tag='benefits'></IndexShare>
  <ShareCommon v-if="showShare" :shareParams='shareParams' @closeShare='closeShare'></ShareCommon>
  <shareBox v-if="showAppShare" @shareAction='shareAction'></shareBox>

</div>
</template>
<script>
import HeaderNav from '@/components/task/header-nav'
import toggleCount from './article-md'
import IndexShare from '@/components/share/IndexShare'
import ShareCommon from '@/components/share/shareCommon'
import * as apiConstant from '@/assets/js/api-constant'
import shareBox from '@/components/common/share_box'
import {mapState, mapMutations} from 'vuex'

export default {
  data () {
    return {
      topData: [],
      listData: [],
      list: [],
      income: [],
      listTab: ['全部', '掘金宝', '金币'],
      indexId: 0,
      count: true,
      imageBaseUrl: '',
      shareParams: null, // 分享参数
      showShare: false, // 显示分享引导
      appShare: {
        path: `/share_page/${Math.ceil(Math.random() * 3)}`
      },
      childParams: {},
      isLoading: false,
      isComplete: false,
      showAppShare: false,
      page: 1
    }
  },
  computed: mapState({
    commonState: state => state.common,
    share: state => state.share
  }),
  methods: {
    ...mapMutations([
      'common_login_box',
      'setShowShare',
      'set_shareSuccessObj',
      'set_shareType'
    ]),
    loadMore () {
      if (this.isComplete) {
        return
      }
      if (this.isLoading) {
        return
      }
      this.isLoading = true
      this.getRecord(this.tasktype, res => {
        this.list = [...this.list, ...res.data]
      })
    },
    // 获取公共分享的文案
    getArc (e) {
      if (e) {
        this.childParams = e
      }
    },
    // 跳转到页面
    toUrl (url) {
      this.$router.push({name: url})
    },
    // 关闭分享引导
    closeShare () {
      this.showShare = false
    },
    shareSuccess ({type, way, success}) {
      this.showShare = false
      if (success !== 2) {
        this.$api.get('CALLBACK_REWARD', {tag: 'benefits'}, res => {
          if (res.data.coin) {
            let data = res.data
            data.type = this.commonState.shareArc['benefits']
            this.set_shareType(16)
            this.set_shareSuccessObj({
              type: 16,
              shareSuccessObj: data
            })
            this.setShowShare(true)
          } else {
            this.$toast({
              message: '分享成功',
              duration: 1500
            })
          }
        })
      }
    },
    shareCallback (id) {
      this.shareSuccess({type: id, way: id, success: 1})
    },
    getShareUrl () {
      this.$api.get('GET_RAMDOMIMAGE', {style: Math.ceil(Math.random() * 4)}, res => {
        this.shareUrl = res.data.img_url
      })
    },
    // 顶部分享
    headerShare () {
      if (this.$config.source_style >= 6) {
        this.showAppShare = true
      } else if (this.$utils.isWx()) {
        this.showShare = true
      } else {
        this.$router.push('/short_link?tag=benefits')
      }
    },
    // 分享操作
    shareAction (actions) {
      if (actions === 'cancel') {
        this.showAppShare = false
      } else {
        this.setAppShare(actions)
      }
    },
    // 设置分享信息msg
    setAppShare (action) {
      let url = this.shareUrl
      console.log(url)
      if (!url) {
        this.$utils.plusReady(plus => {
          plus.nativeUI.confirm('网络出错了，请重试', res => {
            this.getShareUrl()
          }, '提示')
        })
        return
      }
      let msg = {
        type: 'image',
        pictures: [url]
      }
      let flag = true
      let id = ''
      if (action === 'qq') {
        flag = true
        id = 'qq'
        msg = {
          title: this.childParams.title,
          content: this.childParams.desc,
          href: this.childParams.shareLink,
          thumbs: [this.childParams.img_url]
        }
      } else if (action === 'wechat') {
        id = 'weixin'
        flag = true
        msg.extra = {
          scene: 'WXSceneSession'
        }
      } else if (action === 'friend') {
        id = 'weixin'
        msg.extra = {
          scene: 'WXSceneTimeline'
        }
      }
      // qq不需要分享图片
      this.setActure(id, msg, flag)
    },
    // 实际调用分享函数
    setActure (id, msg, flag) {
      let w = window.plus.nativeUI.showWaiting('分享中')
      setTimeout(function () {
        w.close()
      }, 5000)
      this.commonState.shares[id].send(msg, (res) => {
        if (flag) {
          this.shareCallback(id)
        } else {
          this.shareCallback(id)
        }
        this.showAppShare = false
        w.close()
      }, (err) => {
        w.close()
        this.showAppShare = false
        if (flag) {
          if (err.code === -2) {
            this.$toast({
              message: '分享失败',
              duration: 1500
            })
          } else {
            this.$toast({
              message: '分享失败',
              duration: 1500
            })
          }
        } else {
          this.$toast({
            message: '分享失败',
            duration: 1500
          })
        }
      })
    },
    back () {
      this.$router.go(-1)
    },
    openWin () {
      this.$router.push('/burst_rich')
    },
    toggleTab (index) {
      this.indexId = index
      this.page = 1
      this.isComplete = false
      this.list = []
      this.isLoading = true
      if (index === 0) {
        this.getRecord('', res => {
          this.list = res.data
        })
        this.tasktype = ''
      } else if (index === 1) {
        this.getRecord('vcoin', res => {
          this.list = res.data
        })
        this.tasktype = 'vcoin'
      } else {
        this.getRecord('coin', res => {
          this.list = res.data
        })
        this.tasktype = 'coin'
      }
    },
    // 获取钱包记录， type为vcoin掘金宝，coin金币 ''默认为所有
    getRecord (type, cb) {
      let data = {
        tasktype: type,
        page: this.page
      }
      setTimeout(function () {
        this.isLoading = false
      }, 5000)
      this.$api.get('GET_RECORD', data, res => {
        for (let v of res.data.data) {
          if (v.remark.indexOf('<br/>') > -1) {
            let temp = v.remark.split('<br/>')
            v.remark = temp[0] + `<div class="coin">${temp[1]}</title>`
          }
        }
        typeof cb === 'function' && cb(res.data)
        this.isLoading = false
        if (this.page >= res.data.last_page) {
          this.isComplete = true
        }
        this.page++
      }, res => {
        this.isLoading = false
      })
    },
    getInfo () {
      this.$api.get('WALLET_INFO', {}, res => {
        this.topData = res.data
      })
    },
    getIncome () {
      this.$api.get('INCOME', {}, res => {
        this.income = res.data
      })
    }
  },
  mounted () {
    this.getInfo()
    this.getIncome()
    this.getShareUrl()
    this.isLoading = true
    this.getRecord('', res => {
      this.isLoading = false
      this.list = res.data
    })
    // 判断 3次后不显示
    if (this.commonState.userInfo.isNew === 0) {
      // this.count = false
    } else {
      if (this.$utils.cache('threeCount')) {
        if (parseInt(this.$utils.cache('threeCount')) === 1) {
          this.$utils.cache('threeCount', 2)
        } else if (parseInt(this.$utils.cache('threeCount')) === 2) {
          this.$utils.cache('threeCount', 3)
        } else {
          this.count = false
        }
      } else {
        this.$utils.cache('threeCount', 1)
      }
    }
  },
  watch: {
    'commonState.login_box' (flag) {
      if (!flag &&  this.commonState.login_bol) {
        this.page = 1
        this.getInfo()
        this.getIncome()
        this.getShareUrl()
        this.isLoading = true
        this.isComplete = false
        this.list = []
        this.getRecord('', res => {
          this.isLoading = false
          this.list = res.data
        })
      }
    },
    'share.showSuccessShare' (flag) {
      if (!flag && this.share.shareType === 16) {
        this.page = 1
        this.getInfo()
        this.getIncome()
        this.getShareUrl()
        this.isLoading = true
        this.list = []
        this.isComplete = false
        this.getRecord('', res => {
          this.isLoading = false
          this.list = res.data
        })
      }
    }
  },
  components: {
    HeaderNav,
    toggleCount,
    IndexShare,
    ShareCommon,
    shareBox
  },
  filters: {
    newCreat (time) {
      let Ntime = new Date(time)
      let y
      let r
      let h
      let s
      y = (Ntime.getMonth() + 1) > 9 ? (Ntime.getMonth() + 1) : '0' + (Ntime.getMonth() + 1)
      r = Ntime.getDate() > 9 ? Ntime.getDate() : '0' + Ntime.getDate()
      h = Ntime.getHours() > 9 ? Ntime.getHours() : '0' + Ntime.getHours()
      s = Ntime.getMinutes() > 9 ? Ntime.getMinutes() : '0' + Ntime.getMinutes()

      return  y + '-' + r + ' ' + h + ':' + s
    }
  },
  created () {
    this.imageBaseUrl = this.$config.currentApi + apiConstant['GET_RAMDOMIMAGE']
  }
}
</script>
<style lang="scss" scoped>
#profit{
  background: #fff;
  border-top: 1px solid #fff;
  height: 100vh;
  overflow: auto;
  box-sizing: border-box;
  padding-bottom: 10px;
}
.card{
  width: 689px;
  height: 350px;
  position: relative;
  margin: 41px auto 37px auto;
  background: url('../../../assets/img/my/Rectangle.png') no-repeat;
  background-size: 100%;
  color: #ffffff;
  padding: 29px;
  box-sizing: border-box;
  font-size: 24px;
  .title{
    display: flex;
    justify-content: flex-start;
    line-height: 42px;
    font-size: 32px;
    height: 42px;
    align-items: center;
    .moneyIcon{
      width: 32px;
      height: 30px;
      border-radius: 15px;
      background: url('../../../assets/img/my/money.png') no-repeat center top;
      background-size: 100% 100%;
      margin-right: 10px;
    }
    span {
      padding-top: 4px;
    }
  }
  .juejinMoney{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    label {
      font-size: 28px;
    }
    .num{
      font-size: 80px;
      line-height: 97px;
    }
    .tips{
      line-height: 33px;
      font-size: 28px;
    }
  }
  .moneyHistory{
    display: flex;
    justify-content: space-between;
    margin-top: 23px;
    padding: 0;
    line-height: 33px;
    b{
      font-weight: bold;
      margin-left: 20px;
    }
  }
  .bottomText{
    text-align: center;
    line-height: 56px;
    font-size: 24px;
    position: absolute;
    height: 50px;
    bottom: 0;
  }
}

.seeRich{
  width: 750px;
  height: 180px;
  color: #fff;
  box-sizing: border-box;
  text-align: center;
  position: relative;
  img{
    width: 100%;
  }
  div.img{
    width: 100%;
    height: 100%;
  }
  div.click{
    position: absolute;
    top: 85px;
    left: 50%;
    transform: translateX(-50%);
    height: 44px;
    width: 237px;
    border-radius: 40px;
    border: 1px solid #fff;
    font-size: 24px;
    line-height: 44px;
  }
}
.juejinIncomeBreakdown{
  padding-top: 20px ;
  box-sizing: border-box;
  .title{
    font-size: 30px;
    font-weight: bold;
    margin-bottom: 18px;
    margin-left: 30px;
  }
  .tabHeader{
    display: flex;
    justify-content: space-around;
    margin-bottom: 38px;
    div{
      width: 140px;
      height: 60px;
      border-radius: 30px;
      background: #D8D8D8;
      color: #fff;
      font-size: 30px;
      display: flex;
      justify-content: center;
      align-items: center;
      &.active{
        background: #F37F32 100%;
      }
    }
  }
  .list{
    .item{
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 22px 30px 19px;
      // border-bottom: 1px solid #DBDBDB;
      font-size: 24px;
      vertical-align: middle;
      .titleTime {
        >div:first-child {
          line-height: 34px;
          font-size: 30px;
          line-height: 1.3;
        }
        >div:last-child {
          line-height: 34px;
          font-size: 28px;
          margin-top: 4px;
        }
      }
      .rightCoin {
        font-size: 28px;
        line-height: 36px;
        color: #232323;
      }
      div div:nth-child(2){
        margin-top: 10px;
        line-height: 34px;
      }
      div:nth-child(1) div:nth-child(2){
        color: #959595;
      }
      .red{
        color: #E71010;
        font-size: 30px;
        text-align: right;
        line-height: 40px;
      }
    }
  }
  .footer{
    font-size: 24px;
    color: #959595;
    text-align: center;
    padding: 54px 0;
    line-height: 33px;
    margin-bottom: 100px;
  }
  
}
.New-footer{
  height: 100px;
  width: 100%;
  position: fixed;
  bottom: 0;
  background: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-sizing: border-box;
  padding: 0 54px;
  text-align: center;
  font-size: 24px;
  border-top: 1px solid #DBDBDB;
  .New-lt{
    width: 215px;
    height: 80px;
    line-height: 80px;
    box-sizing: border-box;
    border: 1px solid #F38D32;
    border-radius: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    white-space: nowrap;
    background: #fff;
    color: #F38D32;
    > div {
      width: 40px;
      min-width: 40px;
      height: 44px;
      margin-right: 10px;
      .iconSize {
        display: flex;
        align-items: center;
        width: 40px;
        height: 44px;
        // margin-top: -10px;
      }
    }
  }
  .New-rt{
    width: 360px;
    height: 80px;
    line-height: 80px;
    box-sizing: border-box;
    background: #F38D32;
    border-radius: 80px;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    div {
      width: 44px;
      min-width: 44px;
      display: flex;
      margin-right: 10px;
      align-items: center;
      justify-content: center;
      img{
        width: 100%;
        vertical-align: top;
      }
    }
  }
}
</style>



